.primary-button {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 8px 24px;

  background-color: rgba(58, 87, 232, 1);
  border-radius: 6px;

  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 175%;
  color: rgba(255, 255, 255, 1);
}

.primary-button:hover {
  cursor: pointer;
  opacity: 0.85;
}

.second-button {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 8px 24px;

  background-color: rgba(58, 87, 232, 0.2);
  border-radius: 6px;

  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 175%;
  color: rgba(45, 66, 178, 1);
}

.second-button:hover {
  cursor: pointer;
  opacity: 0.85;
}

.disable-button {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 8px 24px;

  background-color: rgba(138, 146, 166, 1);
  border-radius: 6px;

  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 175%;
  color: rgba(255, 255, 255, 1);
}

.sns-button {
  width: 56px;
  height: 56px;
  border-radius: 56px;
}

.sns-button:hover {
  cursor: pointer;
  opacity: 0.9;
}

.toggle {
  width: 35px;
  height: 20px;
  background-image: url('./assets/image/toggle.png');
  background-size: 100% 100%;

  cursor: pointer;
}

.toggle-active {
  width: 35px;
  height: 20px;
  background-image: url('./assets/image/toggle-active.png');
  background-size: 100% 100%;

  cursor: pointer;
}

.text-link {
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 175%;
  color: rgba(113, 113, 133, 1);
}

.text-link:hover {
  cursor: pointer;
  opacity: 0.85;
}

.short-divider {
  width: 64px;
  border: 1px solid rgba(238, 238, 238, 1);
}

.full-width {
  width: 100%;
}

.primary {
  color: rgba(58, 87, 232, 1);
}

.error {
  color: rgba(255, 84, 62, 1);
}

.primary-badge {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 45px;
  height: 20px;

  border-radius: 10px;

  background-color: rgba(58, 87, 232, 1);

  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: rgba(255, 255, 255, 1);
}

.disable-badge {
  display: flex;
  justify-content: center;
  align-items: center;

  width: 45px;
  height: 20px;

  border-radius: 10px;

  background-color: rgba(238, 238, 238, 1);

  font-size: 14px;
  font-weight: 400;
  line-height: 20px;
  color: rgba(138, 146, 166, 1);
}

.error-button {
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  align-items: center;

  padding: 8px 24px;

  background-color: rgba(255, 84, 64, 1);
  border-radius: 6px;

  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 175%;
  color: rgba(255, 255, 255, 1);
}

.error-button:hover {
  cursor: pointer;
  opacity: 0.85;
}